import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';

import { Ingredient } from '../shared/ingredient.model';
import { ShoppingListService } from './shopping-list.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-shopping-list',
  templateUrl: './shopping-list.component.html',
  styleUrls: ['./shopping-list.component.css']
})
export class ShoppingListComponent implements OnInit, OnDestroy {
  ingredients: Ingredient[];
  ingredientSub: Subscription;

  constructor(private shoppinglistservice: ShoppingListService) { }

  ngOnInit() {
    this.ingredients = this.shoppinglistservice.getIngredients();
    this.ingredientSub = this.shoppinglistservice.ingredientsChanged.subscribe((ingredients: Ingredient[]) => {
      this.ingredients = ingredients;
    });
  }

  OnClickIngredient(index: number) {
    this.shoppinglistservice.ingredientSelected.next(index);
  }
  // OnIngredientAdded(ingredient: Ingredient) {
  //   this.shoppinglistservice.IngredientsUpdate(ingredient);
  // }

  ngOnDestroy() {
    this.ingredientSub.unsubscribe();
  }

}
