import { Component, OnInit, ViewChild, Input, OnDestroy } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Ingredient } from 'src/app/shared/ingredient.model';
import { ShoppingListService } from '../shopping-list.service';
import { ActivatedRoute, Params } from '@angular/router';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-shopping-edit',
  templateUrl: './shopping-edit.component.html',
  styleUrls: ['./shopping-edit.component.css']
})
export class ShoppingEditComponent implements OnInit, OnDestroy {
  @ViewChild('f', {static: false}) IngredientForm : NgForm;
  ingredient: Ingredient;
  index: number;
  name: string;
  amount: number;
  editMode = false;
  subscription: Subscription;
  clearconfirm: boolean;

  constructor(private shoppinglistservice: ShoppingListService) { }

  ngOnInit() {
    this.subscription = this.shoppinglistservice.ingredientSelected.subscribe((index: number) => {
      this.index = index
      this.editMode = true;
      this.ingredient = this.shoppinglistservice.getIngredient(this.index);
        this.IngredientForm.setValue({
          name: this.ingredient.name,
          amount: this.ingredient.amount
        });
    })
  }

  OnIngredientAddorUpdate() {
    this.name = this.IngredientForm.value.name;
    this.amount = this.IngredientForm.value.amount;
    const ingredient = new Ingredient(this.name, this.amount);
    if(!this.editMode){
      this.shoppinglistservice.AddIngredient(ingredient);
    }else {
      this.shoppinglistservice.UpdateIngredient(this.index, ingredient);
      this.editMode = false;
    }
    this.IngredientForm.reset();
    // this.IngredientAdded.emit(ingredient);
  }

  OnDeleteIngredient() {
    this.shoppinglistservice.DeleteIngredient(this.index);
    this.editMode = false;
    this.IngredientForm.reset();
  }

  OnClearIngredient() {
    if(!this.ingredient) {
      return;
    }
    this.clearconfirm = window.confirm('Are you sure you want to clear the fields?');
    if(this.clearconfirm){
      this.IngredientForm.reset();
    }else return;
    if(this.editMode){
      this.editMode = false;
    }

  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

}
