import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map, tap } from 'rxjs/operators';
import { RecipeService } from '../recipes/recipe.service';
import { Recipe } from '../recipes/recipe.model';

@Injectable({providedIn: 'root'})

export class DataStorageService {
	constructor(private recipeservice: RecipeService,
				private http: HttpClient) {}

	CreateandStore(){
		const recipes = this.recipeservice.getRecipes();
		this.http.put('https://ng-recipebook-1f43c-default-rtdb.firebaseio.com/recipes.json', recipes).subscribe( responsedata => {
			// console.log(responsedata);
		});
	}

	FetchRecipes() {
		return this.http.get<Recipe[]>('https://ng-recipebook-1f43c-default-rtdb.firebaseio.com/recipes.json').pipe(map(recipes=> {
			return recipes.map( recipe =>{
				return {
					...recipe,
					ingredients: recipe.ingredients ? recipe.ingredients : []
				};
			});
		}),tap(recipes => {
			this.recipeservice.setRecipes(recipes);
		}))
	}
}