import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { FormGroup, FormControl, FormArray, Validators } from '@angular/forms';
import { RecipeService } from '../recipe.service';

@Component({
  selector: 'app-recipe-edit',
  templateUrl: './recipe-edit.component.html',
  styleUrls: ['./recipe-edit.component.css']
})
export class RecipeEditComponent implements OnInit {
  recipeform : FormGroup;
  index: number;
  edit: string;
  editMode = false;

  constructor(private route: ActivatedRoute,
              private recipeservice: RecipeService,
              private router: Router) { }

  ngOnInit() {
    this.route.params.subscribe( (params: Params) => {
      this.index = +params['id'];
      this.editMode = params['id'] != null;
      this.initForm();
      // if (this.index >= 0) {
      //   this.editMode += true;
      // }
    });
  }

  get controls(){
    return(<FormArray>this.recipeform.get('ingredients')).controls;
  }

  initForm(){
    let recipeName = '';
    let description = '';
    let imagePath = '';
    let recipeingredients = new FormArray([]);

    if(this.editMode) {
      const recipe = this.recipeservice.getRecipe(this.index);
      recipeName = recipe.name;
      description = recipe.description;
      imagePath = recipe.imagePath;
      if(recipe['ingredients']) {
        for(let ingredient of recipe.ingredients){
          recipeingredients.push(new FormGroup({
              'name': new FormControl(ingredient.name, Validators.required),
              'amount': new FormControl(ingredient.amount, [Validators.required, Validators.pattern(/^[1-9]+[0-9]*$/)])
          }));
        }
      }
      }

      this.recipeform = new FormGroup({
        'name' : new FormControl(recipeName, Validators.required),
        'description': new FormControl(description, Validators.required),
        'imagePath': new FormControl(imagePath, Validators.required),
        'ingredients': recipeingredients
      })
    }

    OnAddIngredient() {
      (<FormArray>this.recipeform.get('ingredients')).push(
        new FormGroup({
          'name': new FormControl(null, Validators.required),
          'amount': new FormControl(null, [Validators.required, Validators.pattern(/^[1-9]+[0-9]*$/)])
        })
      );
    }

    OnDeleteIngredient(i: number) {
      (<FormArray>this.recipeform.get('ingredients')).removeAt(i);
      console.log(this.recipeform.value);
    }

    OnSubmit(){
      // console.log(this.recipeform.value['recipeName']);
      console.log(this.recipeform.value.recipeName);
      if(this.editMode) {
        this.recipeservice.UpdateRecipe(this.recipeform.value, this.index);
      }else {
        this.recipeservice.AddNewRecipes(this.recipeform.value);
      }
      this.recipeform.reset();
      this.router.navigate(['../'], {relativeTo: this.route});
    }

    OnCancel() {
      this.router.navigate([''], {relativeTo: this.route});
    }

}
